import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import Logo from "../../assets/logo.png";
import api from "../../services/api";
import { logon } from "../../services/auth";
import { Form, Container } from "./styles";
import { userAuth } from '../../actions';

class SignIn extends Component {

  state = {
    login: "",
    password: "",
    error: ""
  };

  handleSignIn = async e => {

    e.preventDefault();

    const { login, password } = this.state;

    if (!login || !password) {

      this.setState({ error: "Preencha e-mail e senha para continuar!" });

    } else {

      try {

        var response = await api.post("/usuario/login", { login, password });

        logon( "Bearer " + response.data['token'] );

        var user = {
          login: login
        };

        this.props.userAuth(user);

        this.props.history.push("/app");

      } catch (err) {

        this.setState({
          error:
            "Houve um problema com o login, verifique suas credenciais!"
        });

      }

    }

  };



  render() {

    return (
      <Container>
        <Form onSubmit={this.handleSignIn}>
          <img src={Logo} alt="Kernel" />
          {this.state.error && <p>{this.state.error}</p>}
          <input
            type="text"
            placeholder="Login"
            onChange={e => this.setState({ login: e.target.value })}
          />
          <input
            type="password"
            placeholder="Senha"
            onChange={e => this.setState({ password: e.target.value })}
          />
          <button type="submit">Entrar</button>
          <a href="/forgotpassword">Esqueceu a Senha?</a>
        </Form>
      </Container>
    );
  }
}

const mapStateToProps = store => ({
  user: store.userAuth.user
});

const mapDispatchToProps = dispatch => bindActionCreators({ userAuth }, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(SignIn));