import React from 'react';

import { layoutStyles } from '../styles';
import KernelNavigation from './../navigation/KernelNavigation';
import KernelMain from '../main/KernelMain';




const KernelLayout = () => {

  const classes = layoutStyles();

  const menu = [
    {
      'id': 'applications',
      'title': 'Applications',
      'type': 'group', // group, collapse, link
      'icon': 'apps',
      'url': '/apps/calendar', //optional
      'children': [
        {
          'id': 'calendar',
          'title': 'Calendar',
          'type': 'item',
          'icon': 'today',
          'url': '/apps/calendar'
        },
        {
          'id': 'divider1',
          'title': 'xxxx',
          'type': 'divider'
        },
        {
          'id': 'project',
          'title': 'Project',
          'type': 'item',
          'url': '/apps/dashboards/project'
        }
      ]
    },

    {
      'id': 'cadastro',
      'title': 'Principal',
      'type': 'group',
      'icon': 'apps',
      'children': [
        {
          'id': 'mnempresa',
          'title': 'Empresa',
          'type': 'item',
          'icon': 'today',
          'url': 'mnEMPRESA'
        },
        {
          'id': 'mnrep',
          'title': 'REP',
          'type': 'item',
          'url': 'mnREP'
        },
        {
          'id': 'mnempregado',
          'title': 'Empregado',
          'type': 'item',
          'url': 'mnEMPREGADO'
        },
        {
          'id': 'mnpessoa',
          'title': 'Pessoa',
          'type': 'item',
          'url': 'mnPESSOA'
        }
      ]
    },

    {
      'id': 'auxiliar',
      'title': 'Auxiliar',
      'type': 'group',
      'icon': 'apps',
      'children': [
        {
          'id': 'mntipodocumentoempresa',
          'title': 'Tipo de Documento de Empresa',
          'type': 'item',
          'icon': 'today',
          'url': 'mnTIPODOCUMENTOEMPRESA'
        },
        {
          'id': 'mntipodocumentopessoa',
          'title': 'Tipo de Documento de Pessoa',
          'type': 'item',
          'url': 'mnTIPODOCUMENTOPESSOA'
        },
      ]
    },

  ];

  return (
    <React.Fragment>
      <div className={classes.root}>

        <KernelNavigation navigation={menu}>
          <KernelMain/>
        </KernelNavigation>
      </div>
    </React.Fragment>
  )

}

export default KernelLayout;